const app = getApp();
import helper from '../componets/helper.js';
import uniRate from '@/componets/wyc-rate/uni-rate.vue';
export default {
	components: { uniRate },
	data() {
		return {
			recList: {},
			navList: [{ title: '发现精彩', img: '/static/index1.png' }, { title: '绘本图书', img: '/static/index2.png' }, { title: '畅销资料', img: '/static/index3.png' }],
			bannerList: [],
			imgUrl: '',
			userInfoButtonShow: true,
			userInfo: {},
			sellList: [],
			bookList: [],
			authorizationButton: null,
			dataCount: 0,
			bookCount: 0,
			curriculaCount: 0
		};
	},
	onLoad() {
		this.userInfo = app.globalData.userInfo;
		this.imgUrl = helper.imgUrl;
		this.onGetUserInfo();
		uni.showShareMenu({
			withShareTicket: true
		});
	},
	onShow() {
		this.banner();
		// 课程推荐
		this.course();
		//畅销资料
		this.sellData();
		//畅销图书
		this.books();
	},
	methods: {
    bannerClick(e) {
      e = e.currentTarget.dataset
      let id = e.id
      let type = e.type
      let goodId = e.goodsid
      if (type == 1) { // 课程
        uni.navigateTo({
        	url: `/pages/indexCourse?id=${goodId}`
        });
      } else if (type == 2) { // 资料
        uni.navigateTo({
        	url: `/pages/indexDataDetail?type=${type}&id=${goodId}`
        });
      } else if (type == 3) { // 图书
        uni.navigateTo({
        	url: `/pages/indexDataDetail?type=${type}&id=${goodId}`
        });
      } else if (type == 4) { // 系统
        uni.navigateTo({
        	url: `/pages/message`
        });
      }
    },
		// 轮播
		banner(callBack) {
			uni.request({
				url: `${helper.requestUrl}index/getBanners`,
				method: 'GET',
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == 'ok') {
						this.bannerList = res.data.data;
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		},
		// 导航
		getNav(e) {
			if (e == '发现精彩') {
				uni.navigateTo({
					url: `/pages/find`
				});
			} else {
				uni.navigateTo({
					url: `/pages/indexAll?type=导航&mode=${e}`
				});
			}
		},
		// 课程推荐
		course() {
			uni.showLoading({
				title: '加载中...'
			});
			uni.request({
				url: `${helper.requestUrl}index/curriculum`,
				method: 'GET',
				data: {
					type: 'index'
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == 'ok') {
						this.recList = res.data.data;
						this.curriculaCount = res.data.count;
					} else {
						this.recList = [];
					}
				}
			});
		},
		goRecList(e) {
			uni.navigateTo({
				url: `/pages/indexCourse?id=${e}`
			});
		},
		// 畅销资料
		sellData() {
			uni.showLoading({
				title: '加载中...'
			});
			uni.request({
				url: `${helper.requestUrl}index/data`,
				method: 'GET',
				data: {
					type: 'index'
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == 'ok') {
						this.sellList = res.data.data;
						this.dataCount = res.data.count;
					} else {
						this.sellList = [];
					}
				}
			});
		},
		// 畅销图书
		books() {
			uni.showLoading({
				title: '加载中...'
			});
			uni.request({
				url: `${helper.requestUrl}index/book`,
				method: 'GET',
				data: {
					type: 'index'
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == 'ok') {
						this.bookList = res.data.data;
						this.bookCount = res.data.count;
					} else {
						this.bookList = [];
					}
				}
			});
		},
		// 获取更多
		getMore(e) {
			if (e == 'recommend') {
				uni.switchTab({
					url: `/pages/classify`
				});
			} else {
				uni.navigateTo({
					url: `/pages/indexAll?type=更多&mode=${e}`
				});
			}
		},
		// 跳转资料详情
		goData(e, id) {
			uni.navigateTo({
				url: `/pages/indexDataDetail?type=${e}&id=${id}`
			});
		},
		// 用户信息校验
		verificationUserInfo() {
			if (!this.userInfo.hasOwnProperty('id')) {
				uni.showToast({
					title: '用户信息加载中...',
					icon: 'none'
				});
				return true;
			}
		},
		// 用户信息获取
		onGetUserInfo() {
			// 查看是否授权
			uni.getSetting({
				success: res => {
					if (res.authSetting['scope.userInfo']) {
						this.userInfoButtonShow = false;
						// 已经授权，可以直接调用 getUserInfo 获取头像昵称
						uni.getUserInfo({
							success: res => {
								console.log(res);
								this.userInfo = Object.assign(this.userInfo, res.userInfo);
								app.globalData.userInfo = this.userInfo;
								// 登录
								this.setUserInfo();
							}
						});
					} else {
						this.setUserInfo();
					}
				}
			});
		},
		// 提交用户信息
		setUserInfo() {
			let that = this;
			uni.login({
				success: res => {
					uni.request({
						url: `${helper.requestUrl}auth/login`,
						method: 'POST',
						data: {
							code: res.code,
							nickname: that.userInfo.nickName,
							sex: that.userInfo.gender,
							headimgurl: that.userInfo.avatarUrl,
							mobile: ''
						},
						success: data => {
							res = helper.null2str(res);
							uni.hideToast();
							console.log(data);
							if (data.statusCode === 200) {
								let token = data.data.token_type + ' ' + data.data.access_token;
								app.globalData.token = token;
								that.getIndexInfo();
							} else {
								uni.showModal({
									title: '',
									content: data.data.errmsg
								});
							}
						}
					});
				}
			});
		},
		getIndexInfo() {
			// 用户信息获取
			uni.request({
				url: `${helper.requestUrl}auth/me`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					if (res.data.status_code == 'ok') {
						this.userInfo = res.data.data;
						app.globalData.userInfo = res.data.data;
					}
				}
			});
		},
		// 搜索
		goSearch() {
			uni.navigateTo({
				url: 'search'
			});
		}
	},
  onShareAppMessage(res) {
    return {
      title: '茂喵喵知识书屋',
      path: '/pages/index'
    }
  }
};
